<template>
  <v-navigation-drawer
    id="core-navigation-drawer"
    v-model="drawer"
    :dark="barColor !== 'rgba(228, 226, 226, 1), rgba(255, 255, 255, 0.7)'"
    :expand-on-hover="expandOnHover"
    :right="$vuetify.rtl"
    :src="barImage"
    mobile-break-point="960"
    app
    width="260"
    v-bind="$attrs"
  >
    <template v-slot:img="props">
      <v-img :gradient="`to bottom, ${barColor}`"  v-bind="props"/>
    </template>

    <v-divider class="mb-1" />

    <v-list
      dense
      nav
    >
      <v-list-item>
        <!-- <v-list-item-avatar class="align-self-center" color="white" contain > -->
          <!-- <v-img src="https://demos.creative-tim.com/vuetify-material-dashboard/favicon.ico" max-height="30"/> -->
        <!-- </v-list-item-avatar> -->

        <v-list-item-content >
          <h2><i>{{profile.title}}</i></h2>
          <!-- <v-list-item-title
            class="display-1"
            v-text="profile.title"
          /> -->
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <v-divider class="mb-2" />

    <v-list expand nav>
      <!-- Style cascading bug  -->
      <!-- https://github.com/vuetifyjs/vuetify/pull/8574 -->
      <div />

      <template v-for="(item, i) in computedItems">
    
        <base-item-group
          v-if="item.children" :key="`group-${i}`" :item="item" >
          <!--  -->
        </base-item-group>

        <base-item v-else :key="`item-${i}`" :item="item" />
      </template>

      <!-- Style cascading bug  -->
      <!-- https://github.com/vuetifyjs/vuetify/pull/8574 -->
      <div />
    </v-list>

    <template v-slot:append>
    <div  @click="logout()">
      <base-item :item="{ title: $t('log_out'),icon: 'el-icon-switch-button' }" />
    </div>
    </template>
  </v-navigation-drawer>
</template>

<script>

  // Utilities
  import { mapState,} from 'vuex'
  import NProgress from 'nprogress'
  import 'nprogress/nprogress.css'

  export default {
    name: 'DashboardCoreDrawer',

    props: {
      expandOnHover: {
        type: Boolean,
        default: false,
      },
    },

    data: () => ({
      items: [
        {
          icon: 'el-icon-menu',
          title: 'dashboard',
          to: '/',
        },
        {
          icon: 'el-icon-s-marketing',
          title: 'sidebar.statistique.title',
          to: 'pages/statistique/report_log',
        },
        {
          icon: 'el-icon-goods',
          title: 'sidebar.commande.tilte',
          children: [
            {
              icon: 'el-icon-goods',
              title: 'sidebar.commande.new-cmd',
              to: 'pages/cmd/new-order'
            },
            {
              icon: 'mdi-account',
              title: 'sidebar.commande.historique',
              to: 'pages/cmd/historique'
            },
            {
              icon: 'mdi-account',
              title: 'sidebar.commande.in_out',
              to: 'pages/cmd/in_out'
            }
          ]
        },
        {
          title: 'sidebar.customer.title',
          icon: 'fas fa-user-group',
          to: '/tables',
          children: [
            {
              icon: 'mdi-account',
              title: 'sidebar.customer.new-customer',
              to: 'pages/customer/new-customer'
            },
            {
              icon: 'mdi-account',
              title: 'sidebar.customer.consult',
              to: 'pages/customer/ManageCustom'
            }
          ]
        },
        {
          title: 'sidebar.service.title',
          icon: 'el-icon-s-unfold',
          to: 'pages/Services/add',
          children: [
            {
              icon: 'mdi-account',
              title: 'sidebar.service.add',
              to: 'pages/Services/add'
            },
            {
              icon: 'mdi-account',
              title: 'sidebar.service.consult',
              to: 'pages/Services/manages'
            }
          ]
        },
        {
          title: 'sidebar.settings.title',
          icon: 'el-icon-setting',
          to: '/components/icons',
          children: [
            {
              icon: 'mdi-account',
              title: 'sidebar.settings.bills',
              to: 'pages/settings/bills'
            },
            {
              icon: 'el-icon-user',
              title: 'sidebar.settings.acount',
              to: 'pages/settings/profile'
            },
            {
              icon: 'mdi-account',
              title: 'sidebar.settings.users',
              to: 'pages/settings/acounts'
            }
          ]
        },
      ],
    }),

    computed: {
      ...mapState(['barColor', 'barImage']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          this.$store.commit('SET_DRAWER', val)
        },
      },
      computedItems () {
        return this.items.map(this.mapItem)
      },
      profile () {
        return {
          avatar: true,
          title: this.$t('avatar'),
        }
      },
    },

    methods: {

      mapItem (item) {

        return {
          ...item,
          children: item.children ? item.children.map(this.mapItem) : '',
          title: this.$t(item.title),
        }
      },


       logout() {
          NProgress.start();
          this.$auth.logout({
                makeRequest: true,
                redirect: {name: 'login'},
          });
          NProgress.done();
        }
    },

    mounted() {
        console.log(this.computedItems);
    }
  }
</script>

<style lang="sass">
  @import '~vuetify/src/styles/tools/_rtl.sass'

  #core-navigation-drawer
    .v-list-group__header.v-list-item--active:before
      opacity: .24

    .v-list-item
      &__icon--text,
      &__icon:first-child
        justify-content: center
        text-align: center
        width: 20px

        +ltr()
          margin-right: 24px
          margin-left: 12px !important

        +rtl()
          margin-left: 24px
          margin-right: 12px !important

    .v-list--dense
      .v-list-item
        &__icon--text,
        &__icon:first-child
          margin-top: 10px

    .v-list-group--sub-group
      .v-list-item
        +ltr()
          padding-left: 8px

        +rtl()
          padding-right: 8px

      .v-list-group__header
        +ltr()
          padding-right: 0

        +rtl()
          padding-right: 0

        .v-list-item__icon--text
          margin-top: 19px
          order: 0

        .v-list-group__header__prepend-icon
          order: 2

          +ltr()
            margin-right: 8px

          +rtl()
            margin-left: 8px
</style>
